@import url("https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@100..900&display=swap");
html,
body {
  font-family: 'Libre Franklin', sans-serif;
  font-optical-sizing: auto;
  font-size: 14px; }

footer.site-footer .social-icons .circle-icon-link {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 1px solid currentColor;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 -7px; }
  footer.site-footer .social-icons .circle-icon-link svg {
    width: 20px;
    height: 20px;
    fill: white; }
  footer.site-footer .social-icons .circle-icon-link:hover {
    background-color: white; }
    footer.site-footer .social-icons .circle-icon-link:hover svg {
      fill: #0479bb; }

@media (min-width: 600px) {
  footer.site-footer .site-footer__grid {
    display: flex;
    gap: unset;
    justify-content: space-between;
    max-width: 1000px;
    margin: 0 auto; }
    footer.site-footer .site-footer__grid > div {
      width: calc(33% - 5rem);
      box-sizing: border-box; } }

form select,
form input {
  padding: 10px;
  min-height: 30px;
  margin: 0;
  box-sizing: border-box;
  margin-top: 10px;
  /* Overrides  and Exceptions */ }
  @media (min-width: 600px) {
    form select,
    form input {
      min-width: 600px; } }
  form select[type='text' i],
  form input[type='text' i] {
    border-radius: 5px;
    display: flex;
    height: 3.75rem;
    width: 100%;
    border: 1px solid #c3c3c3;
    background: #f9f9f9;
    font-weight: 600;
    padding: 0 1em; }
  form select[type='file' i], form select[type='image' i], form select[type='checkbox' i], form select[type='radio'],
  form input[type='file' i],
  form input[type='image' i],
  form input[type='checkbox' i],
  form input[type='radio'] {
    width: auto;
    min-width: auto;
    min-height: auto; }
  form select[type='radio'],
  form input[type='radio'] {
    width: 20px;
    height: 20px;
    transform: translateY(25%);
    margin-right: 10px; }
  form select[name*='zip'],
  form input[name*='zip'] {
    min-width: auto;
    display: inline;
    width: auto; }

form textarea {
  width: 100%;
  padding: 10px;
  min-height: 100px;
  font-size: 22px;
  margin: 0;
  box-sizing: border-box;
  margin-top: 10px; }
  @media (min-width: 600px) {
    form textarea {
      min-width: 600px; } }

form .neoncrm-form-buttons {
  display: flex;
  justify-content: center; }
  form .neoncrm-form-buttons .neoncrm-button.neoncrm-submit {
    background-color: #0479bb;
    font-size: 22px;
    max-width: 300px;
    width: 300px;
    text-transform: uppercase;
    margin-bottom: 40px;
    color: white; }

/* This CSS was stored in Neon */
hr {
  width: 100%;
  height: 4px;
  margin-left: auto;
  margin-right: auto;
  background-color: #0479bb;
  border: 1 none;
  margin-top: 30px;
  margin-bottom: 30px; }

/* customizing general donation form */
.neoncrm-radio-group.neoncrm-donation-levels label {
  font-size: 24px;
  line-height: 48px;
  display: inline-block;
  padding: 5px 15px;
  color: #fff;
  background-color: #0479bb;
  margin: 10px;
  min-width: 48px;
  min-height: 48px;
  border: solid 1px #ddd;
  border-radius: 4px;
  text-align: center; }

.neoncrm-radio-group.neoncrm-donation-levels label:hover {
  background-color: #a6c75f;
  color: #fff; }

.neoncrm-radio-group.neoncrm-donation-levels input[type='radio'] {
  display: none; }

.neoncrm-radio-group.neoncrm-donation-levels input:checked + label {
  background-color: #895ad2;
  color: #fff; }

.neoncrm-donation-levels .neoncrm-radio-button {
  display: inline-block; }

@media (max-width: 600px) {
  .neoncrm-donation-levels .neoncrm-radio-button {
    display: block; }
  .neoncrm-radio-group.neoncrm-donation-levels label {
    display: block; } }

@media (min-width: 768px) {
  #form-wrapper {
    margin: 30px 0 30px 0;
    border: none;
    box-shadow: none; } }

/* modifications to iFrames */
.iframe-container {
  overflow: hidden;
  padding-top: 56.25%;
  position: relative; }

.iframe-container iframe {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%; }

/* 4x3 Aspect Ratio */
